  nav {
    // position: relative;
    // pointer-events: none;
    color: #fff;
    // position: sticky;
    // position: fixed;
    // top: 0;
    // position: absolute;
    // left: 0;
    // padding: 30px;
    // padding-left: 2em;
    padding: 1.5em 0 1.5em 2em;
    width: auto;
    border-left: 2px solid white;

    // &:after {
    //   content: "";
    //   display: block;
    //   border-left: 2px solid white;
    //   height: 100%;
    //   position: absolute;
    //   left: 0;
    //   top: 0;
    // }

    ul {
      height: 100%;
      display: grid;
      grid-template-rows: repeat(13, 1fr);
      align-content: space-evenly;
    }

    li {
      position: relative;
      display: flex;
      align-items: center;
      height: 100%;
      // margin-bottom: 1.5em;
      list-style: none;
      transition: all 0.3s ease-out;

      &:after {
        content: "";
        display: block;
        border-top: 2px solid white;
        width: 1.2em;
        position: absolute;
        left: -2em;
        top: 50%;
        transition: width 0.2s ease-out;
      }

      &.active {
        // pointer-events: none;
        padding-left: 1em;
        // height: 2em;

        &::after {
          width: 2em;
          // top: 1em;
        }

        a {
          font-size: 2em;
          // padding-bottom: 2em;
        }
      }
    }

    a {
      text-decoration: none;
      color: #fff;
      white-space: nowrap;
      transition: all 0.3s ease-out;
    }

    p {
      // margin-top: 2em;

      max-width: 700px;
      height: 0;
      overflow: hidden;
      opacity: 0;
      transition: height 0.3s ease-out, opacity 0.2s ease-out;
    }
  }
}

